import { Http } from 'hyperapp-fx'

export const ChangeNumberOfUsersFetched = (state, evt) => {
  const val = parseInt(evt.target.value)
  if (isNaN(val)) {
    return state
  }
  const newState = { ...state }
  newState.tabs.http.numberOfUsers = val
  return newState
}

export const FetchUsers = (state) => {
  console.log('mama')
  const numberOfUsers = state.tabs.balances.numberOfUsers

  const apiUrl = `https://randomuser.me/api/?results=${numberOfUsers}`

  const windowLocation = new URL(window.location.href)

  const oAuthUrl = 'https://discordapp.com/api/oath2/token'
  const accessCode = windowLocation.searchParams.get('code')

  const clientId = process.env.CLIENT_ID
  const clientSecret = process.env.CLIENT_SECRET
  const redirectUri = process.env.REDIRECT_URI

  const data = new URLSearchParams()

  data.append('client_id', clientId)
  data.append('client_secret', clientSecret)
  data.append('grant_type', 'authorization_code')
  data.append('redirect_uri', redirectUri)
  data.append('scope', 'identify')
  data.append('code', accessCode)

  console.log('accesscode', accessCode)

  console.log('oauthurl', oAuthUrl)
  console.log('data', data)
  return [
    {
      ...state,
      fetchingUsers: true
    },
    Http({
      url: 'https://discordapp.com/api/oath2/token',
      options: {
        method: 'POST',
        body: data
      },
      action: FetchUsersOK,
      error: FetchUserFailed
    })
  ]
}

const FetchUsersOK = (state, { results: users }) => {
  console.log('FetchUsersOK -> users', users)
  const newState = { ...state, fetchingUsers: false }
  newState.tabs.balances.users = users
  return newState
}

const FetchUserFailed = (state, error) => {
  console.log('FetchUserFailed -> error', error)
  return { ...state, fetchingUsers: false }
}
